import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Link, useNavigate } from 'react-router-dom';
import { Form, FormControl, Button, InputGroup, Row, Col, Container,Dropdown, DropdownButton } from 'react-bootstrap';

import newnew from '../images/newnew.PNG';
import cc from '../images/cc.PNG';
import usericon from '../images/user.png';
import searchicon from '../images/search.png';
import helpicon from '../images/helpicon.png';
import displayicon from '../images/displayicon.png';
import logouticon from '../images/logouticon.png';
import bgbg from '../images/bgbg.jpg';

const HeaderCheckpoint = () => {

	  const [nameisloaded, setNameisloaded] = useState(localStorage.getItem('name'));
  const [imageisloaded, setImageisloaded] = useState(localStorage.getItem('image'));
  const [dataLoaded, setDataLoaded] = useState(false);

const isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
	const navigate = useNavigate();


const handleLogout = () => {
		localStorage.setItem('name', '');
		localStorage.setItem('image', '');
		navigate('/appeal/');
	 };

	   useEffect(() => {
    const storedName = localStorage.getItem('name');
    const storedImage = localStorage.getItem('image');

    if (storedName && storedImage) {
      setNameisloaded(storedName);
      setImageisloaded(storedImage);
      setDataLoaded(true);
    } else {
      setDataLoaded(false);
    }
  }, []);
	return (
		<div>
		<section className="headeri" style={{ backgroundColor: '#355797' }}>
			<Container>
		      <Row>
		        <Col xs={4}><div className="logojacenter"><img src={cc} alt="My Image" className="foto" id="articles" /> </div></Col>
		        <Col xs={8}>
		         <div className="positioncenter">
		        	<img src={searchicon} className="searchicona" />
				  </div>
		        </Col>
		      </Row>
		    </Container>
		</section>
		</div>
	)
}

export default HeaderCheckpoint